<template>
    <div class="group-list">
        <div v-if="$route.name === '群发列表'">
            <Table class="group-table" stripe :columns="groupHeader" :data="groupData"></Table>
            <Page show-total :total="dataList.length" :page-size="10" @on-change="changePage" />
        </div>
        <router-view />
    </div>
</template>

<script>
export default {
  data() {
    return {
      groupData: [],
      dataList: [],
      groupHeader: [
        {
          key: "code",
          title: "code",
          align: "center",
          width: 80
        },
        {
          key: "name",
          title: "公号名称",
          align: "center"
        },
        {
          title: "Action",
          align: "center",
          render: (h, params) => {
            let that = this;
            return h("div", [
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$axios.get('/material', {params: {code: params.row.code}})
                      .then(res => {
                        that.$Message.info(res.data.success)
                      })
                    }
                  }
                },
                "同步素材"
              ),
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "success",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$router.push({
                        name: "发送消息",
                        query: { code: params.row.code }
                      });
                    }
                  }
                },
                "群发消息"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    this.showDataList();
  },
  methods: {
    showDataList() {
      this.$axios.get("/conf").then(res => {
        let arr = res.data.data;
        for(let i = 0; i < arr.length; i ++){
            if(arr[i].status == 1) {
                this.dataList.push(arr[i])
            }
        }
        this.groupData = this.dataList.slice(0, 10)
      });
    },
    changePage(index){
      var _start = ( index - 1 ) * 10;
      var _end = index * 10;
      this.groupData = this.dataList.slice(_start,_end);
    }
  }
};
</script>

<style scoped>
.create {
  margin-bottom: 30px;
  float: left;
}

.group-table {
  width: 100%;
  clear: both;
  margin-bottom: 30px;
}
</style>
